.logo {
  font-size: 17px;
  line-height: 21px;
  font-weight: 400;
}

.logo-link {
  color: @basic-white;
  text-decoration: none;
}

.logo__grid {
  display: grid;
  grid-template-columns: min-content 200px;
  column-gap: 29px;
}

.logo__image {
  display: block;
  grid-row-start: 1;
  grid-row-end: 3;
}

.logo p {
  margin: 0;
  padding: 0;
}
